<template>
  <div id="editor">
    <!-- left -->
    <div id="paper" :style="paperSize">
      <component
        :is="currentComponent"
        :roundel="roundel"
      ></component>
    </div>

    <!-- right -->
    <div style="margin:10px;">
      <v-select
        v-model="skin"
        :items="items"
        item-text="name"
        item-value="fileName"
        label="Skin"
        persistent-hint
        return-object
        single-line
      ></v-select>

      <div style="display:flex;flex-direction:column">
        <div v-for="[k, v] of Object.entries(colors)" :key="k">
          <input type="color" :name="k" :value="v" @input="update(k, $event)">
          <label :for="k">{{k}}</label>
        </div>
      </div>

      <roundel-picker v-model="roundel" />
      <v-btn block v-on:click="printOut('paper')">Print</v-btn>
    </div>
  </div>
</template>

<script>
import printJS from "print-js";
import { createFighterComponent } from "@/fighterComponent.js";
import RoundelPicker from "./RoundelPicker.vue";

export default {
  components: { RoundelPicker },
  data() {
    return {
      currentComponent: null,
      roundel: "france",
      width: null,
      height: null,
      paperSize: null,
      skin: { name: "Mirage (1)", fileName: "mirage_skin01" },
      items: [
        { name: "Mirage (1)", fileName: "mirage_skin01" },
        { name: "Mirage (2)", fileName: "mirage_skin02" },
        { name: "F-16 (1)", fileName: "f-16_skin01" },
        { name: "F-16 (2)", fileName: "f-16_skin02" },
      ],
      colors: {},
    };
  },
  async mounted() {
    await this.replace();
  },
  methods: {
    async replace() {
      let fighterName = this.skin.fileName;
      let result = await createFighterComponent(fighterName);
      this.width = result.width;
      this.height = result.height;

      const w = result.width.match(/\d+/g)[0]
      const h = result.height.match(/\d+/g)[0]
      this.paperSize = `width:500px; height:${500.0 * h / w}px;`;

      this.currentComponent = result.component;
      this.colors = result.colors;
    },
    update(prop, event) {
      console.log(`set prop ${prop} with ${event.target.value}`)
      this.colors[prop] = event.target.value;
    },
    printOut(id) {
      printJS({
        printable: id,
        header: this.skin.name,
        type: "html",
        style: `#paper { width:${this.width}; height:${this.height}; margin-left:auto; margin-right: 0;}`,
        scanStyles: false,
      });
    },
  },
  watch: {
    skin: {
      handler(value) {
        this.replace(value.fileName);
      },
    },
  },
};
</script>

<style scoped>
#editor {
  display: flex;
  align-items: top;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}
</style>
