"use strict";

import Vue from "vue";
import { Fighter } from "./fighter.js";

var roundels = require.context("@/assets/roundels", false, /\.svg$/);
var fighters = require.context("@/assets/fighters", false, /\.svg$/);

function roundelAssetPath(roundel) {
  return roundels("./" + roundel + ".svg");
}

function fighterAssetPath(fighter) {
  return fighters("./" + fighter + ".svg");
}

export async function createFighterComponent(fighterName) {
    let [template, colors, width, height] =
      await createFighterTemplate(fighterName);

    let component = Vue.component("fighter", {
      template: template,
      props: {
        colors: { type: Object, default: () => colors },
        roundel: { type: String, default: 'france' },
      },
      data() {
        return {
          target: "base",
          fighterText: "",
          currentComponent: null,
        };
      },
      methods: {
        clickMe() {
          console.log("click");
        },
        setTarget(trg) {
          console.log(`setTarget("${trg}")`);
          this.target = trg;
          // TODO: fire input color open
        },
        getColor(target) {
          return this.colors[target];
        },
        roundelPath(roundel) {
          return roundelAssetPath(roundel);
        },
      },
      watch: {
        color: {
          handler(value) {
            console.log(`watch change taget color(${this.target}, ${value})`);
            var colors = Object.assign({}, this.colors);
            if (typeof value == "string") {
              colors[this.target] = value;
            } else if (value.hex) {
              colors[this.target] = value.hex;
            }

            this.colors = colors;
          },
        },
      },
    });

    return {
        component,
        width,
        height,
        colors,
    }
  }

  async function createFighterTemplate(fighterName) {
    const svgPath = fighterAssetPath(fighterName);
    console.log(svgPath);
    const response = await fetch(svgPath);
    const data = await response.text();
    const fighter = new Fighter(data);

    const width = fighter.svg.getAttribute("width");
    const height = fighter.svg.getAttribute("height");

    fighter.svg.setAttribute("width", "100%");
    fighter.svg.setAttribute("height", "100%");

    for (const [skinId, skin] of Object.entries(fighter.skins)) {
      console.log(skinId);
      for (const [k, v] of Object.entries(skin)) {
        if (v.roundel) {
          v.el.removeAttribute("xlink:href");
          v.el.setAttribute("v-bind:xlink:href", "roundelPath(roundel)");
        }

        if (v.fill) {
          v.el.removeAttribute("fill");
          v.el.setAttribute("v-bind:fill", `getColor("${k}")`);
          v.el.setAttribute("v-on:click.prevent", `setTarget("${k}")`);
        }

        if (v.stroke) {
          v.el.removeAttribute("stroke");
          v.el.setAttribute("v-bind:stroke", `getColor("${k}")`);
          v.el.setAttribute("v-on:click.prevent", `setTarget("${k}")`);
        }
      }
    }

    let colors = {};
    for (const [skinId, skin] of Object.entries(fighter.skins)) {
      console.log(skinId);
      for (const [k, v] of Object.entries(skin)) {
        if (v.fill) {
          colors[k] = v.fill;
        } else if (v.stroke) {
          colors[k] = v.stroke;
        }
      }
    }

    const template = fighter.svg.outerHTML;

    return [template, colors, width, height];
  }