"use strict";

export function Fighter(svgText) {
  [this.svg, this.skins] = parseSvg(svgText);

  function parseSvg(text) {
    const parser = new DOMParser();
    const doc = parser.parseFromString(text, "image/svg+xml");
    const svg = doc.getElementsByTagName("svg")[0];

    const colorMap = findColors(svg);
    const patterns = findPatterns(svg);
    const skins = extractSkins(colorMap, patterns);

    return [svg, skins];
  }

  function findColors(e) {
    function _recursive(e, colorMap) {
      if (e == null) return;

      const colors = extractColors(e);
      if (colors != null) {
        colorMap[e.id] = colors;
      }

      for (const child of e.children) {
        _recursive(child, colorMap);
      }
    }

    function extractColors(e) {
      const fill = extractFill(e);
      const stroke = extractStroke(e);
      const pattern = extractPattern(e);
      const roundel = extractRoundel(e);

      let result = null;
      if (fill != null) {
        result = {
          el: e,
          fill,
        };
      } else if (stroke != null) {
        result = {
          el: e,
          stroke,
        };
      } else if (roundel != null) {
        result = {
          el: e,
          roundel,
        };
      }

      if (result != null && pattern != null) {
        result.pattern = pattern;
      }

      return result;
    }

    function extractFill(e) {
      const fill = e.getAttribute("fill");
      if (typeof fill == "string" && fill != "none") {
        return fill;
      }

      return null;
    }

    function extractStroke(e) {
      const stroke = e.getAttribute("stroke");
      if (typeof stroke == "string" && stroke != "none") {
        return stroke;
      }

      return null;
    }

    function extractPattern(e) {
      const m = e.id.match(/(pattern_\w+)-/);
      if (m == null) return null;
      else return m[1];
    }

    function extractRoundel(e) {
      const m = e.id.match(/(roundel_\w+)$/);
      if (m == null) return null;
      else {
        const roundelPath = e.getAttribute("xlink:href");
        const r = roundelPath.match(/.*\/(\w+)[.]svg$/);
        if (r == null) return null;
        else return r[1];
      }
    }

    const colorMap = {};
    _recursive(e, colorMap);
    return colorMap;
  }

  function findPatterns(e) {
    function _recursive(e, patterns) {
      if (e == null) return null;

      if (typeof e.id == "string" && /pattern_\w+$/.exec(e.id) != null) {
        patterns.push(e);
      }

      for (let child of e.children) {
        _recursive(child, patterns);
      }
    }

    const patterns = [];
    _recursive(e, patterns);
    return patterns;
  }

  function extractSkins(colorMap, patterns) {
    const skins = {};
    patterns.forEach((pattern) => {
      let skin = {};
      for (const [k, v] of Object.entries(colorMap)) {
        if (v.pattern === undefined || v.pattern === pattern.id) {
          // copy
          skin[k] = {
            ...v,
          };
        }
      }
      skins[pattern.id] = skin;
    });

    return skins;
  }
}
